.nav__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  overflow: hidden;
  overflow-x: auto;
  height: 40px;
  align-items: center;
  list-style-type: none;
  gap: 15px; }

.nav__list--decor::after {
  content: "";
  position: absolute;
  align-self: end;
  margin-right: -2px;
  background: linear-gradient(270deg, #F8F8F8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
  width: 26px;
  height: 40px; }

@media (min-width: 1336px) {
  .nav__list {
    margin-top: 20px;
    flex-direction: row;
    flex-wrap: wrap;
    height: auto;
    align-items: flex-start;
    margin-left: 20px; }
  .nav__list--decor::after {
    display: none; } }

.nav__item--active {
  border: 2px solid #B8FFEC;
  border-radius: 5px;
  background-color: #ffffff; }

.nav__link {
  width: auto;
  text-align: center;
  margin-right: 10px;
  padding: 5px 5px 5px 10px;
  height: auto;
  font-size: 16px;
  color: #7E7E82;
  text-decoration: none;
  display: block; }
