.footer {
  color: #7E7E82;
  background-color: #F8F8F8;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 15px;
  align-items: center; }
  @media (min-width: 768px) {
    .footer {
      flex-direction: row;
      justify-content: space-between; } }
  .footer__address {
    text-align: center;
    font-size: 14px;
    width: 218px; }
  @media (min-width: 768px) {
    .footer__address {
      text-align: start; } }
  .footer__link {
    text-decoration: none;
    text-align: center;
    font-size: 14px;
    color: #7E7E82; }
  @media (min-width: 768px) {
    .footer__link {
      text-align: center; } }
  .footer__text {
    text-align: center;
    font-size: 14px;
    width: 312px; }
  @media (min-width: 768px) {
    .footer__text {
      text-align: end;
      width: 213px; } }
  @media (min-width: 1336px) {
    .footer__text {
      width: 330px; } }
