.main {
  background-color: #F8F8F8; }
  .main__title {
    display: flex;
    padding-top: 20px;
    font-size: 28px;
    font-family: 'TT Lakes Medium'; }
  .main__title--decor::before {
    content: "";
    Width: 4px;
    Height: 40px;
    background-color: #41F6D7;
    border-radius: 0px 20px 20px 0px;
    margin-left: 0;
    margin-right: 10px; }
  @media (min-width: 1336px) {
    .main__title {
      margin-top: 20px; } }
  .main__nav {
    margin: 20px 15px 10px 10px; }
