.service {
  display: flex;
  align-items: center;
  justify-content: center; }
  .service__list {
    display: flex;
    list-style-type: none;
    justify-content: space-evenly;
    align-items: center;
    gap: 15px; }
  .service__list--divider::before {
    content: "";
    Width: 2px;
    Height: 32px;
    background-color: #EAEAEA;
    border-radius: 1px;
    margin-right: 5px; }
  @media (min-width: 1336px) {
    .service__list {
      gap: 150px; }
    .service__list--divider::before {
      display: none; } }
