.expand {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: inherit;
  border: none;
  width: 144px;
  padding: 5px;
  color: #1B1C21;
  font-size: 16px; }
  .expand:hover {
    outline: 2px solid #B8FFEC;
    border-radius: 5px; }
  .expand:focus {
    outline: 2px solid #B8FFEC;
    border-radius: 5px; }
  .expand__icon {
    transition: all 1s; }
  .expand__icon--active {
    transform: rotate(180deg); }
