.page__header--active {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1000;
  background-color: rgba(255, 255, 255, 0.9); }
  .page__header--active .header__inner {
    position: relative;
    flex-direction: column;
    align-items: flex-start;
    min-height: 100%;
    width: 100%;
    left: -300px;
    transition: transform 0.5s;
    box-shadow: 20px 0px 50px -5px rgba(14, 24, 80, 0.2); }
  .page__header--active .header__inner--active {
    transform: translateX(300px); }
  @media (min-width: 768px) {
    .page__header--active .header__inner {
      width: 320px; } }
  .page__header--active .header__close {
    display: flex;
    margin: 27px 0 0 20px; }
  .page__header--active .header__burger {
    display: none; }
  .page__header--active .header__logo {
    position: absolute;
    transform: translateX(70px) translateY(20px); }
  .page__header--active .header__logo--divider::before {
    display: none; }
  .page__header--active .header__serch {
    display: flex;
    position: absolute;
    transform: translateX(250px) translateY(27px); }
  .page__header--active .header__header-nav {
    display: flex; }
  .page__header--active .header__feedback {
    display: block;
    margin: auto 15px 0; }
  .page__header--active .header__address {
    display: block; }
  .page__header--active .header__lang {
    display: flex; }
