.communication {
  background-color: rgba(255, 255, 255, 0.9); }
  .communication__inner {
    display: flex;
    flex-direction: column;
    position: relative;
    right: -600px;
    transition: transform 0.5s;
    width: 100%;
    height: 100%;
    background-color: #ffffff; }
  .communication__inner--active {
    transform: translateX(-600px); }
  @media (min-width: 768px) {
    .communication__inner {
      width: 470px;
      margin-left: auto; } }
  .communication__title-modal {
    transform: translateY(50px); }
  .communication__close {
    position: relative;
    top: -30px;
    left: 10px;
    flex: none; }
  @media (min-width: 768px) {
    .communication__close {
      top: 7px;
      left: -65px; } }
  .communication__form {
    margin-top: 20px; }
  .communication__input {
    width: 90%;
    height: 48px;
    font-size: 16px;
    padding: 5px;
    border: 1px solid #EAEAEA;
    border-radius: 5px;
    margin: 10px 10px 0; }
  .communication__area {
    width: 90%;
    height: 118px;
    vertical-align: baseline;
    padding: 5px;
    margin: 10px 10px 0;
    border: 1px solid #EAEAEA;
    border-radius: 5px;
    font-size: 16px; }
  .communication__text {
    margin: 10px 10px 0px; }
  .communication__text--special {
    color: #FF3E79; }
  .communication__confedent {
    color: #FF3E79; }
  .communication__button {
    width: 132px;
    height: 32px;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    text-decoration: none;
    color: white;
    font-family: "TT Lakes Medium";
    padding: 5px;
    margin: 10px 10px 0;
    box-sizing: border-box;
    background-color: #FF3E79;
    border: none;
    align-self: flex-end;
    text-transform: lowercase; }
  .communication__button::after {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    border-bottom: 3.3px solid #ffffff;
    border-right: 3.3px solid #ffffff;
    transform: rotate(315deg);
    transition: transform 0.5s; }
