html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'TT Lakes Regular';
  font-size: 16px; }

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  font-family: 'TT Lakes Regular';
  font-size: 16px; }

.page {
  max-width: 1440px;
  position: relative;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 135px;
  grid-template-areas: "header service" "main main" "footer footer"; }
  .page__header {
    grid-area: header; }
  .page__main {
    grid-area: main; }
  .page__service {
    grid-area: service;
    height: 88px; }
  @media (min-width: 1336px) {
    .page__service {
      height: auto; } }
  .page__footer {
    grid-area: footer;
    border-top: 1px solid #D9FFF5; }
  @media (min-width: 1336px) {
    .page {
      grid-template-columns: 320px 1fr;
      grid-template-areas: "header main" "header footer"; }
      .page__service {
        grid-area: unset;
        position: absolute;
        top: 40px;
        right: 3.2em; } }
  .page__communication {
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 1100;
    display: none; }
  .page__communication--active {
    display: block; }
  .page__call {
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 1100;
    display: none; }
  .page__call--active {
    display: block; }
