@media (min-width: 768px) {
  .about {
    display: flex;
    flex-direction: row; } }

.about__wraper {
  height: auto; }

@media (min-width: 768px) {
  .about__wraper {
    width: 50%; } }

.about__content {
  width: auto;
  height: 95px;
  margin: 20px 20px 0;
  overflow: hidden;
  transition: height 0.8s; }

@media (min-width: 768px) {
  .about__content {
    width: 100%;
    height: 160px;
    margin-top: 0; } }

@media (min-width: 1336px) {
  .about__content {
    width: 490px;
    margin-left: 30px;
    height: 180px; } }

.about__content--active {
  height: 430px; }

@media (min-width: 1336px) {
  .about__content--active {
    height: 350px; } }

.about__text {
  font-size: 14px;
  color: #1B1C21; }

@media (min-width: 768px) {
  .about__text {
    margin-top: 20px; } }

.about__img {
  width: 100%;
  height: auto; }

@media (min-width: 768px) {
  .about__img {
    width: 50%;
    height: 230px;
    margin-top: 20px;
    margin-left: 40px; } }

@media (min-width: 1336px) {
  .about__img {
    width: 40%;
    height: 230px; } }

.about__expand {
  margin: 10px; }

@media (min-width: 1336px) {
  .about__expand {
    margin-left: 30px; } }
