.price__section-title {
  padding: 15px; }

.price__table {
  padding: 15px 20px 0 15px;
  height: 280px; }

@media (min-width: 768px) {
  .price__table {
    height: auto;
    width: 100%; } }

.price__head {
  display: none; }

@media (min-width: 768px) {
  .price__head {
    display: flex; } }

.price__column {
  color: #7E7E82;
  text-align: start;
  vertical-align: bottom;
  font-family: "TT Lakes Regular"; }

.price__body {
  display: flex;
  padding: 5px; }

@media (min-width: 768px) {
  .price__body {
    display: flex;
    flex-direction: column; } }

.price__row {
  display: flex;
  flex-direction: column;
  height: 210px;
  width: 260px;
  background-color: #ffffff;
  padding: 10px;
  justify-content: space-between; }

.price__row--gray {
  background-color: #F8F8F8; }

@media (min-width: 768px) {
  .price__row {
    display: grid;
    grid-template-columns: 5fr 1fr 1fr 1fr;
    width: 100%;
    height: 64px;
    gap: 20px;
    align-items: center;
    justify-content: flex-start;
    padding: 5px;
    box-sizing: border-box;
    vertical-align: middle;
    border-bottom: 1px solid #F8F8F8; } }

.price__description {
  color: #7E7E82; }

@media (min-width: 768px) {
  .price__description {
    display: none; } }

.price__item {
  margin-bottom: 8px; }

.price__icon {
  width: 119px;
  height: 32px;
  background-color: #FF3E79;
  border-radius: 30px;
  align-self: flex-end; }

@media (min-width: 768px) {
  .price__icon {
    align-self: center; } }

.price__link {
  width: 119px;
  height: 32px;
  border-radius: inherit;
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-decoration: none;
  color: #ffffff;
  font-family: "TT Lakes Medium";
  padding: 5px;
  box-sizing: border-box; }

.price__link::after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border-bottom: 3.3px solid #ffffff;
  border-right: 3.3px solid #ffffff;
  transform: rotate(315deg);
  transition: transform 0.5s; }

.price__link:hover::after {
  transform: rotate(135deg); }

.price__link:focus::after {
  transform: rotate(135deg); }

.price__text {
  margin-top: 15px;
  margin-left: 15px;
  width: 288px;
  height: auto;
  color: #1B1C21; }

@media (min-width: 768px) {
  .price__text {
    width: 450px; } }

.price__confedent {
  color: #1B1C21;
  text-decoration: none;
  display: flex;
  align-items: center;
  margin-top: 15px;
  margin-left: 15px;
  margin-bottom: 40px;
  font-family: 'TT Lakes Medium';
  font-size: 16px; }

.price__confedent::after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid #FF3E79;
  border-right: 2px solid #FF3E79;
  margin-left: 5px;
  transform: rotate(315deg);
  transition: transform 0.8s; }

.price__confedent:hover::after {
  transform: translateX(5px) rotate(315deg); }
