.button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #FF3E79;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.7s;
  cursor: pointer; }
  .button::after {
    content: "";
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: absolute; }
  .button:hover {
    opacity: 0.53;
    cursor: pointer;
    transition: all 0.3s;
    box-shadow: 3px 3px 5px 1px #aa3E79; }
  .button:hover::after {
    border: 2px solid #FF3E79;
    transition: transform 0.7s ease-in-out;
    transform: scale(0.8); }
  .button:focus {
    outline: none;
    opacity: 0.53;
    cursor: pointer;
    transition: all 0.3s;
    box-shadow: 3px 3px 5px 1px #aa3E79; }
  .button:focus::after {
    border: 2px solid #FF3E79;
    transition: transform 0.7s ease-in-out;
    transform: scale(0.8); }
  .button:active {
    opacity: 0.9;
    cursor: pointer;
    transition: all 0.1s;
    box-shadow: 0px 0px 5px 0px #553E79 inset, 3px 3px 5px 1px #ff9ee2; }
  .button__text {
    display: none;
    font-family: 'TT Lakes Medium'; }
  @media (min-width: 1336px) {
    .button__text {
      display: block;
      position: absolute;
      transform: translateX(-140px);
      margin-left: 100px;
      width: 150px; } }
