.header-nav__list {
  flex-direction: column;
  flex-wrap: nowrap;
  height: auto;
  align-items: flex-start;
  color: #1B1C21;
  margin-left: 5px;
  overflow: hidden; }

.header-nav__item {
  display: flex;
  justify-content: flex-start;
  margin: 5px; }

.header-nav__link {
  color: #1B1C21;
  transition: all 0.5s;
  display: block;
  padding: 10px;
  width: 100%;
  text-align: start;
  border-radius: 7px;
  text-decoration: none; }

.header-nav__link--active {
  color: #7E7E82;
  margin-left: 10px;
  cursor: default;
  pointer-events: none; }

.header-nav__link--active::before {
  content: "";
  Width: 4px;
  Height: 30px;
  background-color: #41F6D7;
  border-radius: 0px 20px 20px 0px;
  position: absolute;
  transition: all 0.7s;
  transform: translateX(-10px) translateY(-5px); }

.header-nav__link:hover {
  color: #7E7E82;
  transition: all 0.3s;
  background-color: #F9F9f9;
  transform: translateX(10px); }

.header-nav__link:hover::before {
  content: "";
  Width: 4px;
  Height: 30px;
  background-color: #41F6D7;
  border-radius: 0px 20px 20px 0px;
  position: absolute;
  transition: all 0.7s;
  transform: translateX(-10px) translateY(-4px); }

.header-nav__link:focus {
  outline: none;
  color: #7E7E82;
  transition: all 0.3s;
  transform: translateX(10px); }

.header-nav__link:focus::before {
  content: "";
  Width: 4px;
  Height: 30px;
  background-color: #41F6D7;
  border-radius: 0px 20px 20px 0px;
  position: absolute;
  transition: all 0.7s;
  transform: translateX(-10px) translateY(-5px); }

.header-nav__link:active {
  color: #FF3E79;
  transition: all 0.3s; }

.header-nav__link:active::before {
  content: "";
  Width: 4px;
  Height: 30px;
  background-color: #41F6D7;
  border-radius: 0px 20px 20px 0px;
  position: absolute;
  transition: all 0.7s;
  transform: translateX(-10px) translateY(-5px); }

.header-nav__link--active:focus {
  transform: translateX(0px); }
