.brand__section-title {
  padding: 15px; }

.brand__swiper {
  margin: 0 10px;
  height: 130px; }

@media (min-width: 768px) {
  .brand__swiper {
    height: auto;
    margin: 10px 15px 0px;
    overflow-x: visibles; } }

.brand__swiper::after {
  content: "";
  position: absolute;
  background: linear-gradient(270deg, #F8F8F8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
  width: 26px;
  height: 73px;
  top: 15px;
  right: -1px;
  z-index: 10; }

@media (min-width: 768px) {
  .brand__swiper::after {
    display: none; } }

.brand__list {
  display: flex;
  flex-direction: column;
  height: 90px;
  flex-wrap: wrap; }

@media (min-width: 768px) {
  .brand__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    height: 170px;
    gap: 10px 20px;
    transition: all 1s; } }

@media (min-width: 1336px) {
  .brand__list {
    grid-template-columns: 1fr 1fr 1fr 1fr; } }

@media (min-width: 768px) {
  .brand__list--active {
    height: 340px; } }

@media (min-width: 1336px) {
  .brand__list--active {
    height: 257px; } }

.brand__item {
  width: 240px;
  margin-top: 15px;
  height: 72px; }

@media (min-width: 768px) {
  .brand__item {
    margin: 0;
    width: 100%; } }

.brand__link {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  height: 60%;
  padding: 15px;
  cursor: pointer;
  justify-content: space-between;
  width: auto;
  border: 1.5px solid #B8FFEC;
  border-radius: 5px; }

.brand__link:hover .link--icon {
  background-color: #FF3E79; }

.brand__link:hover .link--icon::after {
  border-right: 2px solid white;
  border-top: 2px solid white; }

.brand__expand {
  margin: 20px;
  display: none; }

@media (min-width: 768px) {
  .brand__expand {
    display: flex; } }
