.technique__section-title {
  padding: 15px; }

.technique__swiper {
  margin: 15px 10px 0;
  height: 195px; }

@media (min-width: 768px) {
  .technique__swiper {
    height: auto;
    margin: 10px 15px 0px;
    overflow-x: visibles; } }

.technique__list {
  display: flex;
  flex-direction: column;
  height: 90px;
  flex-wrap: wrap; }

@media (min-width: 768px) {
  .technique__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    height: 170px;
    gap: 10px 20px;
    transition: all 1s; } }

@media (min-width: 1336px) {
  .technique__list {
    grid-template-columns: 1fr 1fr 1fr 1fr; } }

@media (min-width: 768px) {
  .technique__list--active {
    height: 527px; } }

@media (min-width: 1336px) {
  .technique__list--active {
    height: 507px; } }

.technique__item {
  width: 240px;
  height: 160px; }

@media (min-width: 768px) {
  .technique__item {
    width: 100%; } }

.technique__link {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  height: 80%;
  padding: 15px;
  cursor: pointer;
  justify-content: space-between;
  width: auto;
  border: 1.5px solid #B8FFEC;
  border-radius: 5px; }

.technique__text {
  align-self: flex-start; }

.technique__icon {
  align-self: flex-end; }

.technique__link:hover .icon {
  background-color: #FF3E79; }

.technique__link:hover .icon::after {
  border-right: 2px solid #ffffff;
  border-top: 2px solid #ffffff; }

.technique__link:hover .link--icon {
  background-color: #FF3E79; }

.technique__link:hover .link--icon::after {
  border-right: 2px solid #ffffff;
  border-top: 2px solid #ffffff; }

.technique__expand {
  margin: 20px;
  display: none; }

@media (min-width: 768px) {
  .technique__expand {
    display: flex; } }
